/* Responsive styles */

@media only screen and (max-width: 600px) {
  /* Adjust styles for devices with a maximum width of 600px */

  header .container {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }

  nav {
    flex-direction: column;
    align-items: center;
  }

  nav a {
    margin-right: 10px;
    margin-left: 10px;
    margin-top: 10px;
  }

  .home {
    grid-template-columns: 1fr;
    gap: 20px;
  }

  .info-details {
    margin: 10px auto;
    padding: 10px;
  }

  form.signup, form.login, form.forgot-password, form.reset-password {
    max-width: 100%;
    margin: 20px auto;
    padding: 10px;
  }
}

